@font-face {
  font-family: "OpenSans-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.ttf") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.ttf") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.ttf") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  height: 100%;
  background-color: #c7c7c7c7;
}
body {
  min-height: 100%;
  font-family: "OpenSans-Regular", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  background-color: #ffffff;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  text-align: center;
}
.main-header__li {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  list-style: none;
}
.welcome-section-header {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
}
.main-container__li {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
}
.main-container__span-price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
}
.main-container__link-product {
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  text-decoration: none;
  text-transform: uppercase;
}
.main-container__list {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  list-style-type: none;
}
.main-container__section-span {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #000000;
}
.blanks-secondary {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
.blanks-secondary__span {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
}
.blanks-secondary__paragraph {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
.main-container__table {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
